@use './animations';
@import './functions';
@include animations.flash_fade-out-top('.flash-box', .4s, -100%);
@include animations.flash_fade-in-top('.flash-box', .4s, -100%);
@include animations.flash_slide-out-top('.flash-box', .4s, -100%);
@include animations.flash_slide-in-top('.flash-box', .4s, -100%);

.flash-box[template="1"]{
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: #FFFFFF;
    z-index: 1000;
    border-radius: 5px;
    margin: 0;
    padding:15px;
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 300px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.400);
    p{
      font-size:20px;
      margin: 0;
      font-weight:600;
    } 
    .flash-header{
       right:0;
       top:0;
       margin: 5px 0;
       height: 30px;
       width: 100%;
       position: absolute;
       display: flex;
       justify-content: space-between;
       .flash-title{
        height: 100%;
        padding-left: 15px;
       }
    }

    .flash-content{
        position: relative;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        margin-top: 20px;
        svg{
            width: 45px;
            height: 45px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 15px 0;
        }
        .flash-icon{
          margin:0;
        }
        .flash-message{
            color: #444;
            display: flex;
             justify-content: center;
             height: 100%;
             flex-direction: column;
             font-size:15px;
             margin: 0;
         }
         .flash-message ul{
            list-style: none;
            margin: 0;
         }
    }
}
.flash-box[template="1"].flashtype-success{
    border-left: 10px solid #28a745;
    border-right: 10px solid #28a745;
    p,svg{color: #28a745;}
}
.flash-box[template="1"].flashtype-info{
    border-left: 10px solid #284B63;
    border-right: 10px solid #284B63;
    p,svg{color: #284B63;}
}
.flash-box[template="1"].flashtype-warning{
    border-left: 10px solid #FF4000;
    border-right: 10px solid #FF4000;
    p,svg{color: #FF4000;}
}

.flash-box[template="1"].flashtype-danger{
    border-left: 10px solid #dc3545;
    border-right: 10px solid #dc3545;
    svg{color: #dc3545;}
}


.flash-box.top.corner-left{
  top: 0;
  left: 0;
}
.flash-box.top.corner-right{
  top: 0;
  right: 0;
}
.flash-box.top.center{
  top: 0;
  left: calc(100vw / 3.5);
}
.flash-box.bottom.corner-left{
  bottom: 0;
  left: 0;
}
.flash-box.bottom.corner-right{
  bottom: 0;
  right: 0;
}
.flash-box.bottom.center{
  bottom: 0;
}
.flash-box[template="1"].bottom.center{
  left: calc(100vw / 3.5);
}
.flash-box:not(.bottom,.top).center{
  top: calc(100vh / 3);
}
.flash-box[template="1"]:not(.bottom, .top).center{
  left: calc(100vw / 3.5);
}
.flash-box[template="2"]{
  display: flex;
  flex-direction: column;
  position: fixed;
  z-index: 1000;
  width: 100vw;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.400);
  .flash-icon-parent{
     right:0;
     top:0;
     margin: 5px 0 5px 0;
     height: 20px;
     width: 20px;
     position: absolute;
     cursor:pointer;
  }
  .flash-icon-parent{
    margin-right: 5px;
  }
  .flash-icon{
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .flash-content{
      position: relative;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 15px;
      svg{
          width: 20px;
          height: 20px;
          color: #FFF;
          margin: 0 15px;
      }
  }
  .flash-content .flash-message{
     color: #FFF;
     display: flex;
      justify-content: center;
      height: 100%;
      flex-direction: column;
      font-size:15px;
      margin: 0;
      font-weight:600;
  }
  .flash-content .flash-message ul{
      list-style: none;
      margin-bottom: 0;
   }
}
.flash-box[template="2"].flashtype-danger{
  background-color: #dc3545;
}
.flash-box[template="2"].flashtype-warning{
  background-color: #FF4000;
}
.flash-box[template="2"].flashtype-success{
  background-color: #28a745; 
}
.flash-box[template="2"].flashtype-info{
  background-color: #284B63; 
}
.flash-box[template="2"].bottom{
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.flash-box[template="2"].top{
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.flash-box[template="2"]:not(.bottom,.top).center{
  border-radius: 5px;
}
.flash-box[template="1"] [_close_]{
  position:relative;
}
.flash-box[template="2"] [_close_]{
  position:absolute;
  right: 0;
  top: 0;
}
.flash-box[template="2"] [_close_], 
.flash-box[template="1"] [_close_]{
  width: 15px;
  height: 15px;
  margin: 0 5px;
  cursor:pointer;
}
.flash-box[template="2"] [_close_]::before, .flash-box[template="1"] [_close_]::before, 
.flash-box[template="2"] [_close_]::after, .flash-box[template="1"] [_close_]::after {
      position: absolute;
      content: '';
      width: 3px;
      height: 15px;
      background: #333;
      left: 7px;
      border-radius: 5px;
  }
  .flash-box[template="2"] [_close_]::before, 
  .flash-box[template="1"] [_close_]::before{
      transform: rotate(45deg);
  }
  .flash-box[template="2"] [_close_]::after, 
  .flash-box[template="1"] [_close_]::after {
      transform: rotate(315deg);
  }