@mixin flash_slide-in-top($element,$time:0.5s,$top:-100%){
  #{$element}.slide-in-top {
    animation: flash_slide-in-top $time cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
  }

  @keyframes flash_slide-in-top {
    0% {
      transform: translateY($top);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
}

@mixin flash_slide-out-top($element,$time:0.5s,$top:-100%){
  #{$element}.slide-out-top {
    animation: flash_slide-out-top $time cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
  }

  @keyframes flash_slide-out-top {
    0% {
      transform: translateY(0);
      opacity: 1;
    }
    100% {
      transform: translateY($top);
      opacity: 0;
    }
  }
}





@mixin flash_fade-in-top($element, $time, $top){
  #{$element}.fade-in-top {
      animation: flash_fade-in-top $time cubic-bezier(0.390, 0.575, 0.565, 1.000) both running;
  }
  @keyframes flash_fade-in-top {
    0% {
      -webkit-transform: translateY($top);
      transform: translateY($top);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
      opacity: 1;
    }
  }

}


@mixin flash_fade-out-top($element, $time, $bottom){
  #{$element}.fade-out-top {
      animation: flash_fade-out-top $time cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  }
  @keyframes flash_fade-out-top {
    0% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
      opacity: 1;
    }
    100% {
      -webkit-transform: translateY($bottom);
      transform: translateY($bottom);
      opacity: 0;
    }
  }
}